@function getExtensionAssetsPath($asset){
@return '../extensions/CA/MyAccountCustomization/1.0.0/' + $asset;
}

@function getThemeAssetsPath($asset){
@return '../extensions/Kodella/KD_Base_Theme_Landmark_2020/0.0.1/' + $asset;
}

