//TT new styles

.product-details-quantity-options-title {
	text-transform: capitalize;
  	//font-weight: 300 !important;
	margin-bottom: $sc-margin-base * 2;
	display: block;
}
.product-details-quantity-options {
	margin-top:0;
	clear:both;
	width: 15%;
	float: left;
	[data-validation-error="block"] {
		margin-top: $sc-margin-base * 2;
	}

	@media (max-width: $container-tablet) {
		width: 100%;
		text-align: center;
		float: none;
		margin: $sc-margin-lv3 0;

		.product-details-quantity-container {
			display: inline-block;
		}
	}
}

.product-details-quantity-options-title {
	font-weight:$sc-font-weight-bold;
}

.product-details-quantity-options-title-help {
	clear: both;
	display: block;
	font-weight:$sc-font-weight-light;
	margin: $sc-margin-base * 2 0;
}

.product-details-quantity-add,
.product-details-quantity-remove{
	@extend .button-secondary;
	background-color: $sc-color-theme-900;
	border:1px solid $sc-color-theme-900;
	color: $sc-neutral-shade-0;
	width: $sc-target-size-mobile;
	height: $sc-target-size-mobile;

	&:hover, &:active {
		color: $sc-neutral-shade-0;
	}

	@media (min-width: $screen-md-min) {
		display: none;
	}
}
.item-qty-box {
	// border: 1px solid $sc-color-theme-border;
	text-align: center;
	// background: transparent;
	max-width: 70px;
}
.product-details-quantity-value {
	@extend .item-qty-box;
	height: 34px!important;
}
