body {
  font-family: $sc-body-font-family!important;
  font-size: $sc-body-font-size;
  font-style: $sc-body-font-style;
  line-height: $sc-body-line-height;
  letter-spacing: $sc-body-letter-spacing;
  direction: $sc-body-direction;

  @extend %scroll-y;
  overflow-x: hidden;

  @media (min-width: $screen-md-min) {
    font-size: $sc-body-font-size-desktop;
  }
}

// Font styles
//
// Markup:
// <h1>H1 heading</h1><br/>
// <h2>H2 heading</h2><br/>
// <h3>H3 heading</h3><br/>
// <h4>H4 heading</h4><br/>
// <h5>H5 heading</h5><br/>
// <p>Lorem ipsum dolor sit amet</p>
// <p><strong>Lorem ipsum dolor sit amet</strong></p>
// <p><small>Lorem ipsum dolor sit amet</small></p>
// <p><a href="#">Lorem ipsum dolor sit amet</a></p>
//
// Styleguide 2.2.1

h1,
h2,
h3,
h4 {
  font-weight: $sc-font-weight-bold;
  text-transform: capitalize;
}

h1 {
  color: $sc-color-h1;
  font-family: $sc-h1-font-family;
  font-size: $sc-h1-font-size;
  font-style: $sc-h1-font-style;
  text-decoration: $sc-h1-text-decoration;
  line-height: $sc-h1-line-height;
  letter-spacing: $sc-h1-letter-spacing;
  margin-top: $sc-h1-margin-top;
  margin-bottom: $sc-h1-margin-bottom;
  text-transform: $sc-h1-text-transform;

  @media (min-width: $screen-md-min) {
    font-size: $sc-h1-desktop-font-size;
  }
}

h2 {
  color: $sc-color-h2;
  font-family: $sc-h2-font-family;
  font-size: $sc-h2-font-size;
  font-style: $sc-h2-font-style;
  text-decoration: $sc-h2-text-decoration;
  line-height: $sc-h2-line-height;
  letter-spacing: $sc-h2-letter-spacing;
