  text-decoration: $sc-h3-text-decoration;
  line-height: $sc-h3-line-height;
  letter-spacing: $sc-h3-letter-spacing;
  margin-top: $sc-h3-margin-top;
  margin-bottom: $sc-h3-margin-bottom;
  text-transform: $sc-h2-text-transform;

  @media (min-width: $screen-md-min) {
    font-size: $sc-h3-desktop-font-size;
  }
}

h4 {
  color: $sc-color-h4;
  font-family: $sc-h4-font-family;
