  margin-top: $sc-paragraph-margin-top;
  margin-bottom: $sc-paragraph-margin-bottom;
}

small {
  font-size: $sc-font-size-s;
}

b,
strong {
  font-weight: $sc-font-weight-bold;
}

blockquote {
  font-family: $sc-blockquote-font-family;
  color: $sc-color-blockquote;
  font-size: $sc-blockquote-font-size;
  font-style: $sc-blockquote-font-style;
  text-decoration: $sc-blockquote-text-decoration;
  line-height: $sc-blockquote-line-height;
  letter-spacing: $sc-blockquote-letter-spacing;
  margin-top: $sc-blockquote-margin-top;
  margin-bottom: $sc-blockquote-margin-bottom;
  font-weight: $sc-blockquote-font-weight;
  text-transform: $sc-blockquote-text-transform;
}

