//TT new styles
// Header mini cart container
div.header-menu-cart {
	@media (min-width: $screen-md-min) {
		margin-left: 0;
	}
	.header-mini-cart{color:$sc-color-copy;}
	a.header-mini-cart-menu-cart-link {
		height:auto;
		line-height: $sc-padding-lv1 * 7;
		top: 3px;

		&.header-mini-cart-menu-cart-link-enabled {
			background: transparent;
			&:hover{
				background: transparent;
			}
		}
		@media (max-width: $screen-md-min) { padding-top: 3px; top: 2px; }
		// Cart icon
		i.header-mini-cart-menu-cart-icon {
			color: $sc-mobile-header-cart-icon-color;
			line-height: $sc-padding-lv1 * 5;
			font-size: 15px;
			float: left;
			min-width: 32px;
			display: none!important;

			&:hover{ color: $sc-mobile-header-cart-icon-active-color; }
			&:active{ color: $sc-mobile-header-cart-icon-active-color; }

			@media (max-width: $screen-md-min) {
				line-height: 23px;
				font-size: 17px;
			}
		}
		// Item quantity
		span.header-mini-cart-menu-cart-legend{
			margin-left: 0;
			padding-left: 0px;
			display: none;
			color: $sc-header-cart-link-text-color;
		}
	}
}

// Header Mini Cart
div.header-mini-cart {
	border: 1px solid $sc-color-theme-border-light;
	top: 36px;
	&:before, &:after { display: none; }
	// Mini Cart Empty
	div.header-mini-cart-empty {
		a {
			color:$sc-color-copy;
			font-family: $sc-font-family;
			&:hover { text-decoration: underline; }
		}
	}
	// Item
	li.header-mini-cart-item-cell {
		padding-bottom: $sc-padding-lv1 * 3;
		border-bottom: 1px solid $sc-color-theme-border-light;
		// Item Name
		li.header-mini-cart-item-cell-product-title {
			margin-bottom: $sc-padding-lv1;
			text-transform:	uppercase;
			a {
				font-family: $sc-font-family;
				font-weight: $sc-font-weight-semibold;
				color: $sc-color-copy;
				&:hover { color: $sc-color-primary; }
			}
		}
		// Item Price
		li.header-mini-cart-item-cell-product-price { color: $sc-color-primary; }
	}
}

// Buttons
div.header-mini-cart-buttons {
	a.header-mini-cart-button-view-cart,
	a.header-mini-cart-button-checkout {
		@extend .button-primary;
		@extend .button-medium;
		font-size: $sc-font-size-s;
		width: 100%;
		text-align: center;
	}
}

.header-menu-cart-legend {
	background-color: transparent;
}
.header-menu-cart-link {
	// color: $sc-top-menu-link-text-color;
	background-color: transparent;
}

